"use strict";

function id(x) {
  return x;
}




                       
exports.fromObject = id;

                        

exports.stringify = function (j) {
  return JSON.stringify(j);
};

var objToString = Object.prototype.toString;
var objKeys = Object.keys;

function isArray(a) {
  return objToString.call(a) === "[object Array]";
}












































































  
